//
// IMPORTS
//

@import "reset";
@import "variables";
// Syntax highlighting @import is at the bottom of this file

/**************/
/* BASE RULES */
/**************/

html {
  font-size: 100%;
}

body {
	background: #FFCBCB;
  font: 18px/1.4 $helvetica;
  color: #A63030;
  text-align: center
}

.container {
  margin: 0 auto;
  max-width: 740px;
  padding: 0 10px;
  width: 100%;
}

.containerhome {
  margin: 0 auto;
  max-width: 740px;
  padding: 200px 10px;
  width: 100%;
  align-text: center
}

h1, h2, h3, h4, h6 {
  font-family: $helveticaNeue;
  color: #A63030;
  font-weight: bold;

  line-height: 1.7;
  margin: 1em 0 15px;
  padding: 0;

  @include mobile {
    line-height: 1.4;
  }
}

h1 {
  font-size: 30px;
  a {
    color: inherit;
  }
}

h2 {
  font-size: 24px;
}

h3 {
  font-size: 20px;
}

h4 {
  font-size: 18px;
  color: #A63030;
}
h5 {
  font-family: $helveticaNeue;
  font-size: 23px;
  color: #A63030;
  font-weight: bold;
   margin: 10px;
 }

p {
  margin: 15px 0;
}

a {
  color: #A63030;
  text-decoration: none;
	cursor: pointer;
  &:hover, &:active {
    color: white;
  }
}

ul, ol {
  margin: 15px 0;
  padding-left: 30px;
}

ul {
  list-style-type: disc;
}

ol {
  list-style-type: decimal;
}

ol ul, ul ol, ul ul, ol ol {
  margin: 0;
}

ul ul, ol ul {
  list-style-type: circle;
}

em, i {
  font-style: italic;
}

strong, b {
  font-weight: bold;
}

img {
  max-width: 100%;
}

// Fixes images in popup boxes from Google Translate
.gmnoprint img {
  max-width: none;
}

.date {
  font-style: italic;
  color: $gray;
}

// Specify the color of the selection
::-moz-selection {
  color: $black;
  background: $lightGray;
}
::selection {
  color: $black;
  background: $lightGray;
}

// Nicolas Gallagher's micro clearfix hack
// http://nicolasgallagher.com/micro-clearfix-hack/
.clearfix:before,
.clearfix:after {
    content: " ";
    display: table;
}

.clearfix:after {
    clear: both;
}

/*********************/
/* LAYOUT / SECTIONS */
/*********************/

//
// .masthead
//

.wrapper-masthead {
  margin-bottom: 50px;
}

.masthead {
  padding: 20px 0;
  border-bottom: 1px solid #FFFFFF;

  @include mobile {
    text-align: center;
  }
}

.site-avatar {
  float: center;
  width: 70px;
  height: 70px;
  margin-right: 15px;

  @include mobile {
    float: none;
    display: block;
    margin: 0 auto;
  }

  img {
    border-radius: 5px;
  }
}

.site-info {
  float: center;

  @include mobile {
    float: none;
    display: block;
    margin: 0 auto;
  }
}

.home-title {
margin: 0;
  color: #A63030;
  text-align: center;
  cursor: pointer;
  font-family: $helveticaNeue;
  font-weight: 300;
  font-size: 50px;
  letter-spacing: 1px;
}

.home-description {
  margin: 10px 0 0 0;
  color: #A63030;
  text-align: center;
  font-size: 25px;
}

.home-nav {
  text-align: center;
  margin-top: 10px; //
  font-family: $helveticaNeue;
  font-size: 23px;
  font-color: #A63030;
}
.site-name {
  margin: 0;
  color: #A63030;
  text-align: center;
  cursor: pointer;
  font-family: $helveticaNeue;
  font-weight: 300;
  font-size: 28px;
  letter-spacing: 1px;
}

.site-description {
  margin: 10px 0 0 0;
  color: #A63030;
  text-align: center;
  font-size: 16px;

  @include mobile {
    margin: 3px 0;
  }
}

nav {
  text-align: center;
  margin-top: 10px; //
  font-family: $helveticaNeue;
  font-size: 18px;
  font-color: #A63030;

  @include mobile {
    float: none;
    margin-top: 9px;
    display: block;
    font-size: 16px;
  }

  a {
    margin-left: 20px;
    color:  #A63030;
    text-align: right;
    font-weight: 300;
    letter-spacing: 1px;

    @include mobile {
      margin: 0 10px;
      color: #A63030;
    }
  }
}

//
// .main
//

.posts > .post {
  padding-bottom: 2em;
  border-bottom: 1px solid $lightGray;
}

.posts > .post:last-child {
  padding-bottom: 1em;
  border-bottom: none;
}

.post {
  blockquote {
    margin: 1.8em .8em;
    border-left: 2px solid $gray;
    padding: 0.1em 1em;
    color: white;
    font-size: 22px;
    font-style: italic;
  }

  .comments {
    margin-top: 10px;
  }

  .read-more {
    text-transform: uppercase;
    font-size: 15px;
  }
}

.wrapper-footer {
  margin-top: 50px;
  border-top: 1px solid white;
  border-bottom: 1px solid white;
  background-color:#FFCBCB;
}

footer {
  padding: 20px 0;
  text-align: center;
}

.item {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 56.25%;}

.wrapper {
  padding: 1em 1em;
}


.responsive {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;}

.posts {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 200px;
  grid-gap: 1rem;
}

.posts .project {
  // width: auto;
}

.posts .project > .thumb {
  // width: 200px;
  height: 120px;
  background: #A63030;
}

.project h3 {
  font-size: .85em;
  padding: 0 1em;
}

.navicons {
  font-size: 40px;
  font-color: #A63030;

 @media screen and (max-width:330px) {
    float: none;
    margin-top: 9px;
    display: block;
    font-size: 25px;
  }

}
// Settled on moving the import of syntax highlighting to the bottom of the CSS
// ... Otherwise it really bloats up the top of the CSS file and makes it difficult to find the start
@import "highlights";
@import "svg-icons";